// frontend/components/InboxView.js
export default function InboxView({messages=[]}){
  return (
    <div className="space-y-3">
      {messages.length===0 && <div className="text-gray-500">Inbox খালি</div>}
      {messages.map((m,i)=>(
        <div key={i} className="p-3 border rounded-md bg-gray-50">
          <div className="text-sm text-gray-600">{new Date(m.createdAt || m.createdAt).toLocaleString()}</div>
          <div className="mt-1">{m.message}</div>
        </div>
      ))}
    </div>
  )
}
